import { Component, OnInit, ViewChild } from '@angular/core';
import { Router,NavigationExtras } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, App_loginApi, ProjectsApi, Project_photosApi } from '../../../../shared/loopback-sdk';
import { ActivatedRoute } from "@angular/router";
import * as moment from 'moment'; //for date manupalation
import {ConfirmationService} from 'primeng/primeng';
import { Lightbox } from 'angular2-lightbox';
import {PaginatorModule} from 'primeng/primeng';
import {Message,MenuItem,SelectItem} from 'primeng/primeng';
import { CookieService } from 'angular2-cookie/core';
declare var window:any;
declare var $;
declare var sdkPrefix;
declare var hostname:any;
@Component({
	selector: "view Project",
	templateUrl: 'view-project.component.html',
	providers: [ConfirmationService],
})

export class ViewProjectComponent {
	result: any;
	success: boolean;
	display: boolean;
	public id: number;
	public role: string;
	submited:any=false;
	_albums:any = [];
	projectData: any = [];
	backLink:any = "";
	busy:any;
	emptyMessage:String="No Result Found";
	displayConfirm: boolean = false;
	msgs: Message[] = [];
	maxDate:Date;
	loading:boolean = true;
	chapterName:any;
	chapterStatus:any="";
	createdAt:any;
	chapterNameSearch:any;
	chapterStatusSearch:any="";
	createdAtSearch:any;
	paginationVal:any=false;
	loginFlagToken:any;
	perPageLimit:Number = 10;
	offset:Number=0;
	total:number;
	chapterData:any=[];
	ifEmpty:boolean=false;
	constructor(private router:Router, private projectApi:ProjectsApi, private photosApi:Project_photosApi, private _cookieService: CookieService, private _lightbox: Lightbox, private confirmationService: ConfirmationService, private appLoginApi:App_loginApi,private loopAuth:LoopBackAuth,private route: ActivatedRoute) {
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		console.log('this is called');
		let dataObj={};
		let uid=this.id;
		let dataArrObj={
			"project_id":this.id
		}

		this.busy=this.projectApi.getProjects(dataArrObj).subscribe(
			data=>{
				this._albums = [];
				if(data.result.length>0){
					if(data.result[0].status == 1){
						data.result[0].status = 'Active';
					}else{
						data.result[0].status = 'Inactive';
					}
					if(data.result[0].created_date){
						data.result[0].created_date = moment(data.result[0].created_date).format("DD - MMM - YYYY");
					}
					if(data.result[0].project_start_date){
						data.result[0].project_start_date = moment(data.result[0].project_start_date).format("DD - MMM - YYYY");
					}
					if(data.result[0].project_completion_date){
						data.result[0].project_completion_date = moment(data.result[0].project_completion_date).format("DD - MMM - YYYY");
					}
					if(data.result[0].project_cover_photo){
						let img = data.result[0].project_cover_photo;
						data.result[0].project_cover_photo = hostname+"/api/containers/projectImage/download/"+img+'?access_token='+this.loginFlagToken;
						this._albums.push(
							{
								src:data.result[0].project_cover_photo,
								thumb:data.result[0].project_cover_photo
							}
						);
					}
					this.projectData = data.result[0];
					this.getProjectPhotos();
				}
			},
			err=>{

			},
			()=>{

			}
		);
	}

	getProjectPhotos(){
		this.busy=this.photosApi.find({where:{and: [{"project_id":this.id}, {"status":1}]}}).subscribe(
			data=>{
				let result = data;
				if(data.length > 0){
					for(var i=0; i<data.length; i++){
						if(data[i]['project_photo_source']){
							let img = data[i]['project_photo_source'];
							img = "http://phapi.experiencecommerce.com:4500/api/containers/projectImage/download/"+img+'?access_token='+this.loginFlagToken;
							this._albums.push(
								{
									src:img,
									thumb:img
								}
							);
						}
					}

					console.log("albums",this._albums);

				}
			},
			err=>{

			},
			()=>{

			}
		);
	}

	open(index: number): void {
		// open lightbox
		this._lightbox.open(this._albums, index);
	}
}


